const axios = require('axios')

const url = 'tweet/'

class TweetService{
    // get
    static getTweets(){
        return new Promise( (resolve, reject) => {
            try{
                let data = []
                axios.get(url)
                    .then( (response) => {
                        // turn promise response into json
                        data = response.data
                        resolve(data)
                    })
                    .catch(err => console.log(err))
            } catch(err) {
                reject(err)
            }
        })
    }
    // post
    static postTweet(tweet){
        return axios.post(url, {
            tweet
        })
    }
    // delete
    static deleteTweet(id){
        return axios.delete(`${url}${id}`)
    }
}

module.exports = TweetService;