<template>
  <div id="app" class="h-screen">

      <div class="flex flex-col justify-center">
        <img src="./assets/img/twitter.png" alt="" class="object-contain h-48">
        <div class="inline-flex justify-center">
          <input type="text" class="border border-gray-400 py-2 px-5 rounded-md
          focus:outline-none focus:border-blue-700 focus:placeholder-blue-500" 
          placeholder="Compose a tweet" v-model="tweet">
          <button class="py-2 px-4 bg-blue-500 text-white text-bold rounded-md ml-2 hover:bg-blue-700 focus:outline-none"
          v-on:click="postTweet">
            Tweet
          </button>
        </div>
        <div class="inline-flex justify-center mt-5">
          <span class="font-semibold text-xl">
            Latest Tweets
          </span>
        </div>

        <div class="flex flex-col mx-auto mt-5">
          <span class="py-5 px-48 border border-gray-200 bg-gray-200 mb-2 rounded-md bg-blue-200"
          v-for="tweet in reverseTweets" v-bind:key="tweet._id" v-on:dblclick="deleteTweet(tweet._id)">
            {{tweet.tweet}}
          </span>
        </div>
        <empty-component v-if="tweets.length == 0"></empty-component>

      </div>
  </div>
</template>

<script>
import TweetService from './Services/TweetService'
import EmptyComponent from './components/EmptyComponent'
export default {
  name: 'App',
  components: {
    EmptyComponent
  },
  data() {
    return {
      tweets: [],
      tweet: ''
    }
  }, 
  methods: {
    postTweet: async function(){
      await TweetService.postTweet(this.tweet)
      this.tweets = await TweetService.getTweets()
      this.tweet = ''
    },
    deleteTweet: async function(id){
      await TweetService.deleteTweet(id)
      this.tweets = await TweetService.getTweets()
    } 
  },
  computed: {
    reverseTweets: function(){
      return this.tweets.slice().reverse()
    }
  },
  created: async function(){
    try{
      // retrieve tweets
      this.tweets = await TweetService.getTweets()
    } catch(err){
      alert(err + " error in app")
    }
  }
}
</script>

<style>
  @import './assets/css/output.css';
</style>
